.titleSlide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  gap: var(--space-32);
  padding: var(--space-24) var(--space-16);
  position: relative;
  overflow-y: auto; /* –î–æ–±–∞–≤–ª—è–µ–º –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —Å–∫—Ä–æ–ª–ª–∞ */
}

.subtitle {
  font-size: var(--font-size-xl);
  color: var(--color-text-secondary);
  font-weight: var(--font-weight-medium);
  text-align: center;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è —á–∞—Å–æ–≤/–º–µ–º–∞ */
.clockContainer {
  cursor: pointer;
  transition: transform 0.2s ease;
  user-select: none;
}

.clockContainer:hover {
  transform: scale(1.05);
}

/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ —á–∞—Å—ã */
.animatedClock {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-16);
}

.clockFace {
  position: relative;
  width: 200px;
  height: 200px;
  border: 8px solid var(--color-primary);
  border-radius: 50%;
  background: var(--color-background);
  box-shadow: 
    0 0 20px rgba(var(--color-primary-rgb), 0.3),
    inset 0 0 20px rgba(var(--color-primary-rgb), 0.1);
  animation: clockGlow 3s ease-in-out infinite alternate;
}

.clockNumbers {
  position: absolute;
  width: 100%;
  height: 100%;
}

.clockNumbers span {
  position: absolute;
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-bold);
  color: var(--color-primary);
  transform: translate(-50%, -50%);
}

.number12 { top: 15px; left: 50%; }
.number3 { top: 50%; right: 15px; }
.number6 { bottom: 15px; left: 50%; }
.number9 { top: 50%; left: 15px; }

.clockHands {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
}

/* –£–±–∏—Ä–∞–µ–º —á–∞—Å–æ–≤—É—é —Å—Ç—Ä–µ–ª–∫—É */
.hourHand {
  display: none;
}

.minuteHand {
  position: absolute;
  width: 3px;
  height: 70px;
  background: var(--color-primary);
  border-radius: 1.5px;
  left: -1.5px;
  top: -70px;
  transform-origin: 1.5px 70px;
  transform: rotate(90deg);
  animation: minuteRotation 60s linear infinite;
}

.secondHand {
  position: absolute;
  width: 1px;
  height: 80px;
  background: #ff4444;
  left: -0.5px;
  top: -80px;
  transform-origin: 0.5px 80px;
  transform: rotate(0deg);
  animation: secondRotation 1s linear infinite;
}

.clockCenter {
  position: absolute;
  width: 12px;
  height: 12px;
  background: var(--color-primary);
  border-radius: 50%;
  left: -6px;
  top: -6px;
  box-shadow: 0 0 10px rgba(var(--color-primary-rgb), 0.5);
}

.clockLabel {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--color-primary);
  text-align: center;
  animation: labelPulse 2s ease-in-out infinite;
}

/* –ú–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä - —Ç–æ—á–Ω–æ –∫–∞–∫ animatedClock */
.memeContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-16);
  animation: memeAppear 0.5s ease-out;
}

.memeImages {
  display: flex;
  gap: var(--space-16);
  align-items: center;
  justify-content: center;
  width: 80vw;
  max-width: 800px; /* –£–º–µ–Ω—å—à–∞–µ–º –º–∞–∫—Å–∏–º–∞–ª—å–Ω—É—é —à–∏—Ä–∏–Ω—É */
  max-height: 45vh; /* –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º –≤—ã—Å–æ—Ç—É */
}

.memeImage {
  width: calc(40vw - var(--space-8));
  max-width: 350px; /* –£–º–µ–Ω—å—à–∞–µ–º –º–∞–∫—Å–∏–º–∞–ª—å–Ω—É—é —à–∏—Ä–∏–Ω—É –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */
  max-height: 40vh; /* –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º –≤—ã—Å–æ—Ç—É –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */
  height: auto;
  aspect-ratio: 1;
  object-fit: contain;
  border-radius: var(--border-radius-lg);
  border: 3px solid var(--color-primary);
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.15),
    0 0 20px rgba(var(--color-primary-rgb), 0.3);
  transition: transform 0.3s ease;
  animation: memeFloat 2s ease-in-out infinite;
}

/* –£–±–∏—Ä–∞–µ–º —Ç–µ–∫—Å—Ç –º–µ–º–∞ */
.memeText {
  display: none;
}

/* –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤ */
@media (max-width: 768px) {
  .memeImages {
    width: 90vw;
    flex-direction: column;
    gap: var(--space-16);
    max-height: 55vh;
  }
  
  .memeImage {
    width: 250px;
    max-width: 80vw;
    max-height: 25vh;
  }
  
  .authorshipInfo {
    position: relative; /* –ù–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö –¥–µ–ª–∞–µ–º –æ–±—ã—á–Ω—ã–º —ç–ª–µ–º–µ–Ω—Ç–æ–º */
    bottom: auto;
    left: auto;
    transform: none;
    margin-top: var(--space-24);
  }
}

@media (max-width: 480px) {
  .titleSlide {
    gap: var(--space-16);
    padding: var(--space-16);
  }
  
  .memeImage {
    width: 200px;
    max-width: 90vw;
    max-height: 20vh;
  }
  
  .authorshipInfo {
    font-size: var(--font-size-xs);
    padding: var(--space-6) var(--space-12);
    gap: var(--space-8);
  }
}

/* –û–±–µ—Å–ø–µ—á–∏–≤–∞–µ–º, —á—Ç–æ –∫–æ–Ω—Ç–µ–Ω—Ç –Ω–µ –ø–µ—Ä–µ–∫—Ä—ã–≤–∞–µ—Ç—Å—è —Å —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–π –ø–ª–∞—à–∫–æ–π */
@media (min-width: 769px) {
  .titleSlide {
    padding-bottom: 120px; /* –î–æ–±–∞–≤–ª—è–µ–º –æ—Ç—Å—Ç—É–ø —Å–Ω–∏–∑—É –¥–ª—è —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–π –ø–ª–∞—à–∫–∏ */
  }
}

.authorLink {
  color: var(--color-primary);
  text-decoration: none;
  font-weight: var(--font-weight-medium);
  transition: color 0.2s ease;
}

.authorLink:hover {
  color: var(--color-primary-hover);
}

.separator {
  color: var(--color-text-tertiary);
  font-weight: var(--font-weight-medium);
}

.versionText, .buildText {
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-weight: var(--font-weight-medium);
  color: var(--color-text-secondary);
}

/* –ê–Ω–∏–º–∞—Ü–∏–∏ */
@keyframes clockGlow {
  0% { box-shadow: 0 0 20px rgba(var(--color-primary-rgb), 0.3), inset 0 0 20px rgba(var(--color-primary-rgb), 0.1); }
  100% { box-shadow: 0 0 30px rgba(var(--color-primary-rgb), 0.5), inset 0 0 30px rgba(var(--color-primary-rgb), 0.2); }
}

@keyframes minuteRotation {
  from { transform: rotate(90deg); }
  to { transform: rotate(-270deg); }
}

@keyframes secondRotation {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes labelPulse {
  0%, 100% { opacity: 0.8; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.05); }
}

@keyframes memeAppear {
  from { opacity: 0; transform: scale(0.8) translateY(20px); }
  to { opacity: 1; transform: scale(1) translateY(0); }
}

@keyframes memeFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-8px); }
}

/* –£–¥–∞–ª—è–µ–º —Å—Ç–∞—Ä—ã–µ —Å—Ç–∏–ª–∏ */
.repositoryLink,
.repoLink,
.versionInfo,
.versionItem,
.versionLabel,
.versionValue,
.buildDate,
.titleDecoration,
.decorationCircle {
  display: none;
}